<template>
  <div id="lightinfo" class="flex-between flex-stretch" style="height:100%;">

      <div class="position-relative" style="width:150px;border-right:1px solid var(--border-color);">
        <div class="flex-between position-absolute" style="z-index:1;top:0;left:0;right:0;height:40px;
            background-color:var(--bg-color4);border-bottom:1px solid var(--border-color2);">
            <button v-for="item,index in tabs" :key="index" class="tab-btn" 
                :class="currentTabIndex === index? 'active' : ''"
                @click="e=>_toggle_tab(e,index)">{{item.title}}</button>
        </div>
        <el-tree v-if="currentTabIndex === 0" :data="geos" style="overflow-y:auto;height:calc(100% - 40px);margin-top:40px;"
            @node-click="_click_tree"></el-tree>
        <div v-else-if="currentTabIndex === 1">项目</div>
      </div>

      <div style="width:calc(100% - 150px);">
          <div id="map" style="height:100%;"></div>
      </div>

  </div>
</template>

<script>

import originGeos from '../assets/js/china_address_v4';

export default {
    data:()=>{
        return {
            tabs: [
                { title: '项目区域' },
                { title: '项目' }
            ],
            currentTabIndex: 0,
            geos: [],
            map: null
        }
    },
    computed: {
        currentTab: function(){
            return this.tabs[this.currentTabIndex];
        }
    },
    mounted(){
        this.formatGeo();
        this.initMap();
    },
    methods: {
        /**
         * 格式化成 <el-tree> 能直接使用的数据
         */
        formatGeo(){
            
            if(!(originGeos instanceof Array)) return;
            
            let geos = [];
            originGeos.forEach(v=>{
                if(undefined === v.parent){
                    geos.push({ label: v.name, value: v.value, children: [] });
                }else{
                    let geoIndex = geos.findIndex(vv=>vv.value === v.parent);
                    if(-1 !== geoIndex) geos[geoIndex].children.push({ label: v.name, value: v.value, children: [] });
                    else{
                        let geoIndex;
                        let geoChildrenIndex;
                        for(let kk in geos){
                            let vv = geos[kk];
                            let index = vv.children.findIndex(vvv=>vvv.value === v.parent);
                            if(-1 !== index){
                                geoIndex = 1*kk;
                                geoChildrenIndex = index;
                                break;
                            }
                        }
                        if(undefined !== geos[geoIndex]?.children[geoChildrenIndex]?.children)
                        geos[geoIndex].children[geoChildrenIndex].children.push({ label: v.name, value: v.value, children: [] });
                    }
                }
            })
            this.geos = geos;
            
        },
        /**
         * 初始化地图
         */
        initMap(){
            let qq = window.qq;
            this.map = new qq.maps.Map(document.getElementById("map"), {
                // 地图的中心地理坐标。
                center: new qq.maps.LatLng(39.916527,116.397128),
                zoom: 21
            });
        },
        _toggle_tab(e,index){
            this.currentTabIndex = index;
        },
        _click_tree(data,node){
            
            if(node.childNodes.length > 0) return;

            let addr = node.parent.parent.data.label + node.parent.data.label + node.data.label;

            let qq = window.qq;
            let geocoder = new qq.maps.Geocoder();
            let map = this.map;

            geocoder.setComplete(function(result) {
                map.setCenter(result.detail.location);
            });
            //若服务请求失败，则运行以下函数
            geocoder.setError(function() {
                alert("出错了，请输入正确的地址！！！");
            });

            geocoder.getLocation(addr);
        }
    }    
}
</script>

<style>
    #lightinfo .tab-btn {
        width: 50%;
        cursor: pointer;
    }
    #lightinfo .tab-btn.active {
        text-decoration: underline var(--ft-color2);
    }
</style>